}

.colorBox {
    width: 20px;
    height: 20px;
   background-color: aqua;
  }

.item {
  display: flex;
  flex-wrap: wrap;
}

.allBoxes {
  position: relative;
  margin-top: 50px;
  left : -30px;
}

.buttonReset {
  position: fixed;
  height : 80px;
  width : 80px;
  margin-top: auto;
  margin-bottom: auto;
  margin-left: 50px;
  margin-right: 50px;
}

.markClassName, .trackClassName, .thumbClassName {
  color : rgb(160, 185, 243);
  border: 4px, solid, rgb(46, 107, 110);
  background-color: rgb(37, 0, 46);
}

.vertical-slider {
  background-color: blueviolet;
  margin-top: auto;
  margin-bottom: auto;
  margin-left : 30px;
  margin-right: 30px;
  font-size: large;
  color:yellow;
